import { ref } from 'vue'
import jobs from '@/helpers/jobs'
import aboutMe from '@/helpers/aboutMe'
import { useStore } from "vuex"
import emailValidate from "@/helpers/emailValidate";

const useLanding = () => {
    const store = useStore()
    let mainText = ref('Hola, soy creador de apliaciones web.')
    let buttonText = ref('Descubre ms.')
    let projects = ref(jobs)
    let aboutMeContent  = ref(aboutMe)
    let socialLinks = ref({
        linkedin: 'https://www.linkedin.com/in/lorenzorojo12/',
        twitter: 'https://twitter.com/lorenzodev20',
        github: 'https://github.com/lorenzodev20',
        medium: 'https://medium.com/@lorenzodev2020'
    })

    const checkToken = async () => {
        const token = localStorage.getItem('tokenEmail')
        if (!token){
            await store.dispatch('login')
        }
    }

    const emailSend = async ({ name, email, subject, message }) => {
        const result = emailValidate(email)
        if (!result.rps) {
           return result
        }
        if (!name || name.length === 0) {
            return {
                rps: false,
                message: 'El nombre no puede estar vaci'
            };
        }
        if (!message || message.length === 0) {
            return {
                rps: false,
                message: 'El mensaje no puede estar vaci'
            };
        }
        const data = await store.dispatch('sendEmail',{ name, email, subject, message })
        data['rps'] = true
        return data
    }
    
    return {
        mainText,
        buttonText,
        projects,
        aboutMeContent,
        checkToken,
        emailSend,
        socialLinks
    }
}

export default useLanding